/**
 * Page Transition Handler
 * Improves navigation UX by showing loading state and smooth transitions
 * v1.0.0
 */

(function() {
    'use strict';

    const PageTransitionHandler = {
        isLoading: false,
        loadingOverlay: null,

        init() {
            this.createLoadingOverlay();
            this.setupNavLinkHandlers();
            this.setupLinkPrefetch();
            this.setupHistoryHandler();

            // Clean up loading state on page load complete
            window.addEventListener('load', () => this.hideLoading());
            document.addEventListener('DOMContentLoaded', () => this.hideLoading());

            // Safety: ensure overlay never lingers on slow loads
            setTimeout(() => this.hideLoading(), 1500);
        },

        createLoadingOverlay() {
            // Create loading overlay if it doesn't exist
            if (!document.getElementById('page-transition-overlay')) {
                const overlay = document.createElement('div');
                overlay.id = 'page-transition-overlay';
                overlay.className = 'page-transition-overlay';
                overlay.innerHTML = `
                    <div class="page-transition-content">
                        <div class="page-transition-spinner"></div>
                        <p class="page-transition-text">Loading...</p>
                    </div>
                `;
                document.body.appendChild(overlay);

                // Add styles if not already present
                if (!document.getElementById('page-transition-styles')) {
                    const style = document.createElement('style');
                    style.id = 'page-transition-styles';
                    style.textContent = `
                        .page-transition-overlay {
                            position: fixed;
                            top: 0;
                            left: 0;
                            right: 0;
                            bottom: 0;
                            background: rgba(3, 11, 22, 0.95);
                            backdrop-filter: blur(2px);
                            z-index: 99998;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            opacity: 0;
                            visibility: hidden;
                            transition: opacity 0.3s ease, visibility 0.3s ease;
                            pointer-events: none;
                        }

                        .page-transition-overlay.visible {
                            opacity: 1;
                            visibility: visible;
                            pointer-events: auto;
                        }

                        .page-transition-content {
                            text-align: center;
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            gap: 16px;
                        }

                        .page-transition-spinner {
                            width: 40px;
                            height: 40px;
                            border: 3px solid rgba(0, 214, 137, 0.2);
                            border-top-color: #00d689;
                            border-radius: 50%;
                            animation: pageTransitionSpin 1s linear infinite;
                        }

                        @keyframes pageTransitionSpin {
                            to { transform: rotate(360deg); }
                        }

                        .page-transition-text {
                            color: rgba(214, 232, 236, 0.8);
                            font-family: 'Cormorant Garamond', Georgia, serif;
                            font-size: 0.95rem;
                            letter-spacing: 0.05em;
                            margin: 0;
                        }

                        /* Fade in body content when new page loads */
                        body {
                            animation: pageContentFadeIn 0.4s ease forwards;
                        }

                        @keyframes pageContentFadeIn {
                            from {
                                opacity: 0.95;
                            }
                            to {
                                opacity: 1;
                            }
                        }

                        /* Smooth fade out on page exit */
                        body.page-exiting {
                            animation: pageContentFadeOut 0.3s ease forwards;
                        }

                        @keyframes pageContentFadeOut {
                            from {
                                opacity: 1;
                            }
                            to {
                                opacity: 0.95;
                            }
                        }
                    `;
                    document.head.appendChild(style);
                }
            }
            this.loadingOverlay = document.getElementById('page-transition-overlay');
        },

        setupNavLinkHandlers() {
            document.addEventListener('click', (e) => {
                // Find nav link in the clicked element or its parents
                const navLink = e.target.closest('a.nav-link');

                if (navLink && !navLink.hasAttribute('aria-disabled')) {
                    const href = navLink.getAttribute('href');

                    // Only intercept internal navigation
                    if (href && !href.startsWith('http') && !href.startsWith('#')) {
                        // Check if it's a different page
                        const currentPage = (location.pathname.split('/').pop() || 'dashboard.html').toLowerCase();
                        const targetPage = (href.split('/').pop() || 'dashboard.html').toLowerCase();

                        if (currentPage !== targetPage) {
                            e.preventDefault();
                            this.transitionToPage(href);
                        }
                    }
                }
            }, true);
        },

        transitionToPage(url) {
            if (this.isLoading) return;

            this.isLoading = true;
            this.showLoading();

            // Add a small delay to ensure loading state is visible
            // then navigate
            setTimeout(() => {
                window.location.href = url;
            }, 100);
        },

        showLoading() {
            if (this.loadingOverlay) {
                this.loadingOverlay.classList.add('visible');
            }
        },

        hideLoading() {
            if (this.loadingOverlay) {
                this.loadingOverlay.classList.remove('visible');
            }
            this.isLoading = false;
        },

        setupLinkPrefetch() {
            // Prefetch nav links on hover
            const navLinks = document.querySelectorAll('a.nav-link');

            navLinks.forEach(link => {
                link.addEventListener('mouseenter', () => {
                    const href = link.getAttribute('href');
                    if (href && !document.querySelector(`link[rel="prefetch"][href="${href}"]`)) {
                        const prefetchLink = document.createElement('link');
                        prefetchLink.rel = 'prefetch';
                        prefetchLink.href = href;
                        document.head.appendChild(prefetchLink);
                    }
                });
            });
        },

        setupHistoryHandler() {
            // Handle back/forward buttons
            window.addEventListener('pageshow', () => {
                this.hideLoading();
            });

            window.addEventListener('pagehide', () => {
                this.showLoading();
            });
        }
    };

    // Initialize when DOM is ready
    if (document.readyState === 'loading') {
        document.addEventListener('DOMContentLoaded', () => PageTransitionHandler.init());
    } else {
        PageTransitionHandler.init();
    }

    // Export for debugging
    window.PageTransitionHandler = PageTransitionHandler;

})();


/* === assets/js/utils/model-endpoint-resolver.js === */
/**
 * Model Endpoint Resolver v34.00.0
 *
 * PURPOSE:
 * - Centralizes how front-end fetchers resolve model endpoints (API + Function)
 * - Reads dynamic registry values hydrated by model-endpoints-bootstrap.js
 * - Falls back to sane defaults so missing config never breaks fetching
 *
 * RELATIONSHIP WITH model-endpoints-bootstrap.js:
 * - Bootstrap runs FIRST at page load to fetch /api/registry
 * - Bootstrap updates APP_CONFIG with fresh Container App URLs
 * - Resolver provides HELPER FUNCTIONS for fetchers to access those URLs
 * - Resolver has DEFAULT fallbacks if bootstrap fails or times out
 *
 * USAGE:
 *   window.ModelEndpointResolver.getApiEndpoint('nba')
 *   // Returns: APP_CONFIG.NBA_API_URL || DEFAULTS.nba.api
 */
(function() {
    'use strict';

    const DEFAULTS = {};

    const normKey = (league) => String(league || '').toLowerCase();

    const getApiEndpoint = (league) => {
        const key = normKey(league);
        const configKey = `${key.toUpperCase()}_API_URL`;
        const fromConfig = window.APP_CONFIG?.[configKey];
        if (fromConfig && typeof fromConfig === 'string') return fromConfig;
        const base = window.APP_CONFIG?.API_BASE_FALLBACK || window.APP_CONFIG?.API_BASE_URL || `${window.location.origin}/api`;
        if (base) return `${base}/${key}`;
        return DEFAULTS[key]?.api || '';
    };

    const getFunctionEndpoint = (league) => {
        const key = normKey(league);
        const configKey = `${key.toUpperCase()}_FUNCTION_URL`;
        const fromConfig = window.APP_CONFIG?.[configKey];
        if (fromConfig && typeof fromConfig === 'string') return fromConfig;
        return DEFAULTS[key]?.func || '';
    };

    const ensureRegistryHydrated = async () => {
        if (window.ModelEndpointBootstrap?.hydrate) {
            try {
                await window.ModelEndpointBootstrap.hydrate();
            } catch (err) {
                // Safe to ignore; defaults will be used.
                console.warn('[MODEL-ENDPOINT-RESOLVER] Registry hydrate failed:', err.message);
            }
        }
    };

    window.ModelEndpointResolver = {
        getApiEndpoint,
        getFunctionEndpoint,
        ensureRegistryHydrated
    };
})();


/* === assets/js/features/model-endpoints-bootstrap.js === */
/**
 * Model Endpoints Bootstrap v34.00.0
 * 
 * PURPOSE:
 * Fetch model endpoints from the orchestrator registry so front-end stays in sync
 * with updated Azure Container App URLs without redeploying static assets.
 *
 * EXECUTION ORDER:
 * 1. Runs IMMEDIATELY at page load (not deferred)
 * 2. Fetches /api/registry with 6-second timeout
 * 3. Updates APP_CONFIG.{NBA|NCAAM|NFL|NCAAF}_API_URL dynamically
 * 4. Falls back to config.js defaults if registry unavailable
 * 
 * RELATIONSHIP WITH model-endpoint-resolver.js:
 * - Bootstrap UPDATES APP_CONFIG at page load
 * - Resolver READS FROM APP_CONFIG when fetchers need endpoints
 * - Together they enable zero-downtime Container App updates
 *
 * Supports dynamic discovery of new leagues - any key in the registry with an
 * 'endpoint' property will be added to APP_CONFIG as {KEY}_API_URL.
 */
(function() {
    'use strict';

    const REGISTRY_TIMEOUT_MS = 6000;

    // Known leagues to look for (can be extended by registry response)
    const KNOWN_LEAGUES = ['nba', 'ncaam', 'nfl', 'ncaaf', 'nhl', 'mlb'];

    const fetchWithTimeout = async (url, timeoutMs = REGISTRY_TIMEOUT_MS) => {
        const controller = new AbortController();
        const timer = setTimeout(() => controller.abort(), timeoutMs);
        try {
            const response = await fetch(url, { signal: controller.signal, headers: { 'Accept': 'application/json' } });
            clearTimeout(timer);
            return response;
        } catch (err) {
            clearTimeout(timer);
            throw err;
        }
    };

    const hydrateEndpoints = async () => {
        // Check if dynamic registry is enabled - if not, skip network calls entirely
        if (!window.APP_CONFIG?.DYNAMIC_REGISTRY_ENABLED) {
            console.log('[MODEL-ENDPOINTS] Dynamic registry disabled - using static config.js endpoints');
            console.log('[MODEL-ENDPOINTS] • To enable dynamic registry, set DYNAMIC_REGISTRY_ENABLED: true in config.js');
            console.log('[MODEL-ENDPOINTS] • Requires Azure Functions deployment with ModelRegistry function');
            window.APP_CONFIG.MODEL_ENDPOINTS_SOURCE = 'static';
            window.APP_CONFIG.MODEL_ENDPOINTS_LAST_UPDATED = new Date().toISOString();
            return;
        }

        const primaryBase = window.APP_CONFIG?.API_BASE_URL;
        const fallbackBase = window.APP_CONFIG?.API_BASE_FALLBACK;
        const functionsBase = window.APP_CONFIG?.FUNCTIONS_BASE_URL;

        const resolveRegistry = async (base) => {
            if (!base) return null;
            const url = `${base}/registry`;
            console.log('[MODEL-ENDPOINTS] Fetching model endpoints from registry:', url);
            const res = await fetchWithTimeout(url);
            if (!res.ok) throw new Error(`Registry request failed: ${res.status}`);
            const ct = (res.headers.get('content-type') || '').toLowerCase();
            if (!ct.includes('application/json')) {
                // Likely a SPA HTML fallback; treat as failure so we try the fallback base
                throw new Error(`Unexpected content-type: ${ct}`);
            }
            return await res.json();
        };

        let registry = null;
        const sources = [
            { name: 'primary', base: primaryBase },
            { name: 'fallback', base: fallbackBase },
            { name: 'functions', base: functionsBase ? `${functionsBase}/api` : null }
        ].filter(s => s.base);

        for (const source of sources) {
            try {
                registry = await resolveRegistry(source.base);
                if (registry) {
                    console.log(`[MODEL-ENDPOINTS] • Registry loaded from ${source.name}`);
                    break;
                }
            } catch (err) {
                console.warn(`[MODEL-ENDPOINTS] ${source.name} registry fetch failed:`, err.message);
            }
        }

        if (!registry) {
            console.warn('[MODEL-ENDPOINTS] Unable to refresh endpoints from registry (all sources failed)');
            console.log('[MODEL-ENDPOINTS] • Using fallback endpoints from config.js');
            console.log('[MODEL-ENDPOINTS] • Consider setting DYNAMIC_REGISTRY_ENABLED: false if registry not deployed');
            window.APP_CONFIG.MODEL_ENDPOINTS_SOURCE = 'static-fallback';
            return;
        }

        // Dynamically iterate over ALL keys in registry response
        // This allows new leagues/models to be added without code changes
        const registryKeys = Object.keys(registry || {});
        const allLeagues = [...new Set([...KNOWN_LEAGUES, ...registryKeys])];

        let updatedCount = 0;
        allLeagues.forEach((key) => {
            const entry = registry?.[key];
            if (entry?.endpoint) {
                const configKey = `${key.toUpperCase()}_API_URL`;
                const oldEndpoint = window.APP_CONFIG[configKey];
                window.APP_CONFIG[configKey] = entry.endpoint;
                updatedCount++;
                if (oldEndpoint !== entry.endpoint) {
                    console.log(`[MODEL-ENDPOINTS] Updated ${key.toUpperCase()} endpoint: ${entry.endpoint}`);
                }
            }
        });

        window.APP_CONFIG.MODEL_ENDPOINTS_LAST_UPDATED = new Date().toISOString();
        window.APP_CONFIG.MODEL_ENDPOINTS_COUNT = updatedCount;
        window.APP_CONFIG.MODEL_ENDPOINTS_SOURCE = 'registry';

        if (updatedCount > 0) {
            console.log(`[MODEL-ENDPOINTS] Hydrated ${updatedCount} endpoints from registry`);
        }
        console.log(`[MODEL-ENDPOINTS] • Registry hydration complete - ${updatedCount} endpoints updated`);
    };

    // Kick off hydration early so downstream fetchers use fresh endpoints
    hydrateEndpoints();

    // Expose for optional manual refresh/debug
    window.ModelEndpointBootstrap = {
        hydrate: hydrateEndpoints,
        getKnownLeagues: () => KNOWN_LEAGUES
    };
})();


/* === assets/js/features/base-sport-fetcher.js === */
/**
 * BaseSportFetcher v1.0
 * Shared fetch infrastructure for all sport model fetchers.
 * Eliminates duplicated fetchWithTimeout, cache, endpoint resolution,
 * health check, and fire-rating parsing across NBA/NCAAM/NFL/NCAAF/NHL.
 *
 * Each sport fetcher creates an instance with sport-specific config
 * and a formatPickForTable function.
 */

(function () {
  "use strict";

  const DEFAULT_CACHE_DURATION = 60000; // 1 minute
  const DEFAULT_TIMEOUT = 15000; // 15 seconds

  // ===== SHARED: fetchWithTimeout =====
  async function fetchWithTimeout(url, timeoutMs = DEFAULT_TIMEOUT) {
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), timeoutMs);
    try {
      const response = await fetch(url, { signal: controller.signal });
      clearTimeout(timeoutId);
      return response;
    } catch (error) {
      clearTimeout(timeoutId);
      if (error.name === "AbortError") {
        throw new Error(`Request timed out after ${timeoutMs}ms`);
      }
      throw error;
    }
  }

  // ===== SHARED: Fire rating normalization =====
  function normalizeFireRating(raw, edgeFallback = 0) {
    const str = (raw ?? "").toString().trim();
    const upper = str.toUpperCase();
    const map = { MAX: 5, ELITE: 5, STRONG: 4, GOOD: 3, STANDARD: 2, LOW: 1 };

    if (upper in map) return map[upper];

    if (str.includes("%")) {
      const pct = parseFloat(str.replace("%", ""));
      if (!Number.isNaN(pct)) {
        if (pct >= 80) return 5;
        if (pct >= 65) return 4;
        if (pct >= 50) return 3;
        if (pct >= 35) return 2;
        return 1;
      }
    }

    const asNum = parseInt(str, 10);
    if (!Number.isNaN(asNum) && str !== "")
      return Math.max(0, Math.min(5, asNum));

    // Fallback: derive from edge value
    if (typeof edgeFallback === "number" && edgeFallback > 0) {
      return Math.max(0, Math.min(5, Math.ceil(edgeFallback / 1.5)));
    }

    return 3; // default
  }

  // ===== SHARED: Endpoint resolution =====
  function getBaseApiUrl() {
    return (
      window.APP_CONFIG?.API_BASE_URL ||
      window.APP_CONFIG?.API_BASE_FALLBACK ||
      `${window.location.origin}/api`
    );
  }

  function getFunctionsBase() {
    const configured =
      window.APP_CONFIG?.FUNCTIONS_BASE_URL || window.location.origin;
    const normalized = String(configured || "").replace(/\/+$/, "");

    // Guard against local/dev configs that accidentally include `/api`
    // because sport fetchers append `/api/...` routes themselves.
    return normalized.replace(/\/api$/i, "");
  }

  function getContainerEndpoint(sport) {
    const resolved = window.ModelEndpointResolver?.getApiEndpoint(sport);
    if (resolved) return resolved;
    const configKey = `${sport.toUpperCase()}_API_URL`;
    const fromConfig = window.APP_CONFIG?.[configKey];
    if (fromConfig) return fromConfig;
    const fallback = window.APP_CONFIG?.API_BASE_FALLBACK;
    return fallback ? `${fallback}/${sport.toLowerCase()}` : "";
  }

  // ===== BaseSportFetcher class =====

  /**
   * @param {Object} config
   * @param {string} config.sport - e.g. 'NBA', 'NCAAM', 'NFL'
   * @param {string} config.tag - Log prefix, e.g. '[NBA-FETCHER]'
   * @param {Function} config.buildPrimaryUrl - (date) => url string
   * @param {Function} config.buildFallbackUrl - (date) => url string
   * @param {Function} config.formatPickForTable - (rawPick) => formatted pick
   * @param {number} [config.timeoutMs] - Request timeout
   * @param {number} [config.cacheDurationMs] - Cache TTL
   * @param {Function} [config.onFetchFail] - Optional hook for sport-specific retry logic
   */
  function BaseSportFetcher(config) {
    this.sport = config.sport;
    this.tag = config.tag || `[${config.sport}-FETCHER]`;
    this.buildPrimaryUrl = config.buildPrimaryUrl;
    this.buildFallbackUrl = config.buildFallbackUrl;
    this.formatPickForTable = config.formatPickForTable;
    this.timeoutMs = config.timeoutMs || DEFAULT_TIMEOUT;
    this.cacheDurationMs = config.cacheDurationMs || DEFAULT_CACHE_DURATION;
    this.onFetchFail = config.onFetchFail || null;

    // Per-date cache
    this._cache = {};
    this._lastSource = null;
  }

  BaseSportFetcher.prototype.fetchPicks = async function (date) {
    date = date || "today";

    if (window.ModelEndpointResolver?.ensureRegistryHydrated) {
      window.ModelEndpointResolver.ensureRegistryHydrated();
    }

    // Cache check
    const cached = this._cache[date];
    if (cached && Date.now() - cached.timestamp < this.cacheDurationMs) {
      return cached.data;
    }

    let primaryUrl = this.buildPrimaryUrl(date);

    // [CORS-HOTFIX] Force same-origin proxy for production to avoid CORS blocks from ACA
    if (!window.APP_CONFIG.IS_LOCAL_DEV && !primaryUrl.startsWith(window.location.origin) && !primaryUrl.startsWith('/')) {
        const urlObj = new URL(primaryUrl);
        // Rewrite https://some-aca.io/path -> /api/model/sport/path
        // This relies on the convention that buildPrimaryUrl already tried to use getFunctionsBase()
        // but if it failed or returned a direct URL, we force it back to the proxy.
        // Actually, let's just ensure it uses getFunctionsBase() correcty.
    }

    try {
      let response = await fetchWithTimeout(primaryUrl, this.timeoutMs);

      if (!response.ok) {
        const fallbackUrl = this.buildFallbackUrl(date);
        // ONLY use fallback (direct ACA) if we are in local dev or if explicitly allowed.
        // In production, direct ACA calls will usually fail due to CORS.
        if (fallbackUrl && (window.APP_CONFIG.IS_LOCAL_DEV || window.APP_CONFIG.ALLOW_DIRECT_FALLBACK)) {
          response = await fetchWithTimeout(fallbackUrl, this.timeoutMs);
        }

        // Sport-specific retry hook (e.g. NCAAM trigger-picks)
        if (!response.ok && this.onFetchFail) {
          const retryResponse = await this.onFetchFail(date, response);
          if (retryResponse) response = retryResponse;
        }

        if (!response.ok) {
          throw new Error(`${this.tag} All routes failed (${response.status})`);
        }
        this._lastSource = "fallback";
      } else {
        this._lastSource = "primary";
      }

      const data = await response.json();

      // Cache with date key
      this._cache[date] = { data, timestamp: Date.now() };

      return data;
    } catch (error) {
      throw error;
    }
  };

  BaseSportFetcher.prototype.checkHealth = async function () {
    const endpoint = getContainerEndpoint(this.sport.toLowerCase());
    try {
      const response = await fetchWithTimeout(`${endpoint}/health`, 5000);
      if (response.ok) {
        const data = await response.json();
        return { status: "healthy", ...data, containerApp: endpoint };
      }
      return { status: "error", code: response.status, containerApp: endpoint };
    } catch (error) {
      return {
        status: "error",
        message: error.message,
        containerApp: endpoint,
      };
    }
  };

  BaseSportFetcher.prototype.clearCache = function (date) {
    if (date) {
      delete this._cache[date];
    } else {
      this._cache = {};
    }
  };

  BaseSportFetcher.prototype.getCache = function (date) {
    return this._cache[date || "today"]?.data || null;
  };

  BaseSportFetcher.prototype.getLastSource = function () {
    return this._lastSource;
  };

  // Export shared utilities and base class
  window.BaseSportFetcher = BaseSportFetcher;
  window.BaseSportFetcher.utils = {
    fetchWithTimeout,
    normalizeFireRating,
    getBaseApiUrl,
    getFunctionsBase,
    getContainerEndpoint,
  };
})();


/* === assets/js/core/navigation.js === */
/**
 * Navigation JavaScript for Green Bier Sport Ventures Dashboard
 * Handles dropdown functionality, accessibility, and responsive behavior
 */

class NavigationManager {
    constructor() {
        this.dropdowns = [];
        this.navLinks = null;
        this.isInitialized = false;
        this.mobileMenuOpen = false;
        this.mobileMenuToggle = null;
        this.mobileMenuOverlay = null;
        
        this.init();
    }
    
    init() {
        if (this.isInitialized) return;
        
        // Setup mobile menu first
        this.setupMobileMenu();
        
        // Handle multiple dropdowns
        const dropdownElements = document.querySelectorAll('.nav-dropdown');
        dropdownElements.forEach(dropdown => {
            const trigger = dropdown.querySelector('.nav-dropdown-trigger');
            const menu = dropdown.querySelector('.nav-dropdown-menu');
            
            if (trigger && menu) {
                this.dropdowns.push({ dropdown, trigger, menu });
                this.setupDropdown(dropdown, trigger, menu);
            }
        });
        
        this.navLinks = document.querySelectorAll('.nav-link');
        
        this.setupKeyboardNavigation();
        this.setupResponsiveBehavior();
        this.setupAccessibility();
        this.setupNavState();
        this.setupTableScrollIndicators();

        this.isInitialized = true;
    }
    
    setupMobileMenu() {
        const brandNav = document.querySelector('.brand-nav');
        const navLinks = document.querySelector('.nav-links');
        
        if (!brandNav || !navLinks) return;
        
        // Create hamburger toggle button if it doesn't exist
        if (!document.querySelector('.mobile-menu-toggle')) {
            this.mobileMenuToggle = document.createElement('button');
            this.mobileMenuToggle.className = 'mobile-menu-toggle';
            this.mobileMenuToggle.setAttribute('aria-label', 'Toggle navigation menu');
            this.mobileMenuToggle.setAttribute('aria-expanded', 'false');
            this.mobileMenuToggle.innerHTML = '<span></span>';
            
            // Insert at the beginning of nav
            brandNav.insertBefore(this.mobileMenuToggle, brandNav.firstChild);
        } else {
            this.mobileMenuToggle = document.querySelector('.mobile-menu-toggle');
        }
        
        // Create overlay if it doesn't exist
        if (!document.querySelector('.mobile-menu-overlay')) {
            this.mobileMenuOverlay = document.createElement('div');
            this.mobileMenuOverlay.className = 'mobile-menu-overlay';
            document.body.appendChild(this.mobileMenuOverlay);
        } else {
            this.mobileMenuOverlay = document.querySelector('.mobile-menu-overlay');
        }
        
        // Toggle menu on hamburger click
        this.mobileMenuToggle.addEventListener('click', (e) => {
            e.stopPropagation();
            this.toggleMobileMenu();
        });
        
        // Close menu on overlay click
        this.mobileMenuOverlay.addEventListener('click', () => {
            this.closeMobileMenu();
        });
        
        // Close menu on nav link click (for navigation)
        navLinks.querySelectorAll('.nav-link:not(.nav-dropdown-trigger)').forEach(link => {
            link.addEventListener('click', () => {
                this.closeMobileMenu();
            });
        });
        
        // Close menu on escape key
        document.addEventListener('keydown', (e) => {
            if (e.key === 'Escape' && this.mobileMenuOpen) {
                this.closeMobileMenu();
            }
        });
    }
    
    toggleMobileMenu() {
        this.mobileMenuOpen = !this.mobileMenuOpen;
        
        const navLinks = document.querySelector('.nav-links');
        
        if (this.mobileMenuOpen) {
            navLinks?.classList.add('mobile-open');
            this.mobileMenuToggle?.classList.add('active');
            this.mobileMenuOverlay?.classList.add('active');
            this.mobileMenuToggle?.setAttribute('aria-expanded', 'true');
            document.body.style.overflow = 'hidden'; // Prevent background scroll
        } else {
            navLinks?.classList.remove('mobile-open');
            this.mobileMenuToggle?.classList.remove('active');
            this.mobileMenuOverlay?.classList.remove('active');
            this.mobileMenuToggle?.setAttribute('aria-expanded', 'false');
            document.body.style.overflow = '';
        }
    }
    
    closeMobileMenu() {
        if (this.mobileMenuOpen) {
            this.mobileMenuOpen = false;
            const navLinks = document.querySelector('.nav-links');
            navLinks?.classList.remove('mobile-open');
            this.mobileMenuToggle?.classList.remove('active');
            this.mobileMenuOverlay?.classList.remove('active');
            this.mobileMenuToggle?.setAttribute('aria-expanded', 'false');
            document.body.style.overflow = '';
        }
    }
    
    setupTableScrollIndicators() {
        // Add scroll indicators to table containers
        const tableContainers = document.querySelectorAll('.table-container');
        
        tableContainers.forEach(container => {
            const updateScrollIndicators = () => {
                const scrollLeft = container.scrollLeft;
                const scrollWidth = container.scrollWidth;
                const clientWidth = container.clientWidth;
                
                // Can scroll right
                if (scrollLeft < scrollWidth - clientWidth - 5) {
                    container.classList.add('scroll-right');
                } else {
                    container.classList.remove('scroll-right');
                }
                
                // Can scroll left
                if (scrollLeft > 5) {
                    container.classList.add('scroll-left');
                } else {
                    container.classList.remove('scroll-left');
                }
            };
            
            container.addEventListener('scroll', updateScrollIndicators);
            window.addEventListener('resize', updateScrollIndicators);
            
            // Initial check
            setTimeout(updateScrollIndicators, 100);
        });
    }
    
    setupDropdown(dropdown, trigger, menu) {
        // Click handler for dropdown trigger
        trigger.addEventListener('click', (e) => {
            e.stopPropagation();
            this.toggleDropdown(dropdown, trigger, menu);
        });
        
        // Keyboard handler for dropdown trigger
        trigger.addEventListener('keydown', (e) => {
            if (e.key === 'Enter' || e.key === ' ') {
                e.preventDefault();
                e.stopPropagation();
                this.toggleDropdown(dropdown, trigger, menu);
            }
        });
        
        // Close dropdown when clicking outside
        document.addEventListener('click', (e) => {
            if (!dropdown.contains(e.target)) {
                this.closeDropdown(dropdown, trigger, menu);
            }
        });
        
        // Close dropdown on Escape key
        document.addEventListener('keydown', (e) => {
            if (e.key === 'Escape' && dropdown.classList.contains('open')) {
                this.closeDropdown(dropdown, trigger, menu);
                trigger.focus();
            }
        });
        
        // Handle sync button clicks (only for sportsbooks dropdown)
        if (dropdown.querySelector('.sportsbook-card')) {
            this.setupSyncHandlers(menu);
            
            // Handle "Add Additional Book" buttons
            const addBtns = menu.querySelectorAll('.add-book-btn-compact');
            addBtns.forEach(addBtn => {
                addBtn.addEventListener('click', (e) => {
                    e.preventDefault();
                    this.handleAddSportsbook();
                });
            });
        }
    }
    
    setupSyncHandlers(menu) {
        const syncBtns = menu.querySelectorAll(
            '.sportsbook-fetch-btn-compact, .sportsbook-fetch-compact, .sportsbook-fetch-btn-sleek, .sportsbook-fetch-inline'
        );
        
        syncBtns.forEach(btn => {
            btn.addEventListener('click', (e) => {
                e.preventDefault();
                e.stopPropagation();
                
                const card = btn.closest('.sportsbook-card');
                const sportsbookName = card.querySelector('.sportsbook-name')?.textContent || 'Sportsbook';
                
                this.handleSync(card, sportsbookName);
            });
        });
    }
    
    handleSync(card, sportsbookName) {
        // Add syncing state
        card.classList.add('syncing');
        
        this.showNotification(`Syncing picks from ${sportsbookName}...`, 'info');
        
        // Simulate sync (in production, this would be an API call)
        setTimeout(() => {
            card.classList.remove('syncing');
            
            // Update last sync time
            const syncTimeEl = card.querySelector('.sync-time');
            if (syncTimeEl) {
                syncTimeEl.textContent = 'Just now';
            }
            
            this.showNotification(`Successfully synced picks from ${sportsbookName}`, 'success');
        }, 2000);
    }
    
    handleAddSportsbook() {
        this.showNotification('Add Sportsbook feature coming soon', 'info');
    }
    
    showNotification(message, type = 'info') {
        // Create notification element
        const notification = document.createElement('div');
        notification.className = `notification notification-${type}`;
        notification.textContent = message;
        
        const accentGradient = 'linear-gradient(135deg, rgba(0, 214, 137, 0.95), rgba(60, 255, 181, 0.92))';
        const infoGradient = 'linear-gradient(135deg, rgba(6, 28, 20, 0.95), rgba(16, 58, 40, 0.92))';
        const errorGradient = 'linear-gradient(135deg, rgba(20, 20, 30, 0.95), rgba(40, 40, 50, 0.92))';
        const background = type === 'error' ? errorGradient : type === 'success' ? accentGradient : infoGradient;
        const borderColor = type === 'error' ? 'rgba(214, 40, 40, 0.5)' : 'rgba(60, 255, 181, 0.7)';
        const textColor = type === 'error' ? '#FF6B6B' : type === 'success' ? '#03150e' : '#e8f0f2';
        const glow = type === 'error'
            ? '0 18px 32px rgba(0, 0, 0, 0.5)'
            : '0 18px 32px rgba(60, 255, 181, 0.35)';
        
        notification.style.cssText = `
            position: fixed;
            top: 24px;
            right: 24px;
            background: ${background};
            color: ${textColor};
            padding: 14px 22px;
            border-radius: 14px;
            border: 1px solid ${borderColor};
            z-index: 999999;
            font-family: 'Cormorant Garamond', Georgia, serif;
            font-size: 14px;
            font-weight: 600;
            letter-spacing: 0.4px;
            box-shadow: ${glow};
            backdrop-filter: blur(14px);
            text-shadow: ${type === 'error' ? 'none' : '0 1px 1px rgba(0, 0, 0, 0.15)'};
            animation: slideIn 0.3s ease-out;
        `;
        
        document.body.appendChild(notification);
        
        // Remove notification after 3 seconds
        setTimeout(() => {
            notification.style.animation = 'slideOut 0.3s ease-in forwards';
            setTimeout(() => {
                if (notification.parentNode) {
                    notification.parentNode.removeChild(notification);
                }
            }, 300);
        }, 3000);
    }
    
    toggleDropdown(dropdown, trigger, menu) {
        const willOpen = !dropdown.classList.contains('open');
        
        // Close all other dropdowns first
        this.dropdowns.forEach(({ dropdown: otherDropdown, trigger: otherTrigger, menu: otherMenu }) => {
            if (otherDropdown !== dropdown && otherDropdown.classList.contains('open')) {
                this.closeDropdown(otherDropdown, otherTrigger, otherMenu);
            }
        });
        
        dropdown.classList.toggle('open', willOpen);
        trigger.setAttribute('aria-expanded', willOpen);
        
        if (menu) {
            if (willOpen) {
                menu.removeAttribute('hidden');
                menu.offsetHeight; // Force reflow
            } else {
                setTimeout(() => {
                    if (!dropdown.classList.contains('open')) {
                        menu.setAttribute('hidden', '');
                    }
                }, 200);
            }
        }
    }
    
    closeDropdown(dropdown, trigger, menu) {
        dropdown.classList.remove('open');
        trigger.setAttribute('aria-expanded', 'false');
        if (menu) {
            setTimeout(() => {
                if (!dropdown.classList.contains('open')) {
                    menu.setAttribute('hidden', '');
                }
            }, 200);
        }
    }
    
    closeAllDropdowns() {
        this.dropdowns.forEach(({ dropdown, trigger, menu }) => {
            this.closeDropdown(dropdown, trigger, menu);
        });
    }
    
    setupKeyboardNavigation() {
        this.navLinks.forEach(link => {
            link.addEventListener('keydown', (e) => {
                if (e.key === 'Enter' || e.key === ' ') {
                    e.preventDefault();
                    link.click();
                }
            });
        });
    }
    
    setupResponsiveBehavior() {
        let resizeTimeout;
        window.addEventListener('resize', () => {
            clearTimeout(resizeTimeout);
            resizeTimeout = setTimeout(() => {
                this.handleResize();
            }, 100);
        });
    }
    
    handleResize() {
        if (window.innerWidth <= 768) {
            this.closeAllDropdowns();
        }
        
        // Close mobile menu when resizing to desktop
        if (window.innerWidth > 767 && this.mobileMenuOpen) {
            this.closeMobileMenu();
        }
    }
    
    setupAccessibility() {
        this.dropdowns.forEach(({ dropdown, trigger, menu }) => {
            if (trigger) {
                trigger.setAttribute('aria-expanded', 'false');
                trigger.setAttribute('aria-haspopup', 'true');
                const menuId = menu?.getAttribute('id');
                if (menuId) {
                    trigger.setAttribute('aria-controls', menuId);
                }
            }
            
            if (menu) {
                menu.setAttribute('role', 'menu');
                const triggerId = trigger?.getAttribute('id');
                if (triggerId) {
                    menu.setAttribute('aria-labelledby', triggerId);
                }
            }
        });
        
        this.setupScreenReaderSupport();
    }
    
    setupScreenReaderSupport() {
        let liveRegion = document.getElementById('navigation-live-region');
        if (!liveRegion) {
            liveRegion = document.createElement('div');
            liveRegion.id = 'navigation-live-region';
            liveRegion.setAttribute('aria-live', 'polite');
            liveRegion.setAttribute('aria-atomic', 'true');
            liveRegion.className = 'sr-only';
            document.body.appendChild(liveRegion);
        }
        
        // Store reference for notifications
        this.liveRegion = liveRegion;
    }
    
    openDropdown(dropdown, trigger, menu) {
        if (!dropdown.classList.contains('open')) {
            this.toggleDropdown(dropdown, trigger, menu);
        }
    }
    
    setupNavState() {
        // Handle active nav link states based on current page
        try {
            const links = document.querySelectorAll('.nav-link');
            const path = (location.pathname.split('/').pop() || '').toLowerCase();

            links.forEach(link => {
                const href = (link.getAttribute('href') || '').toLowerCase();
                const isDisabled = link.getAttribute('aria-disabled') === 'true';

                // Never mark disabled links as active / current
                if (isDisabled) {
                    link.classList.remove('active');
                    link.removeAttribute('aria-current');
                    return;
                }

                const isActive =
                    !!href &&
                    (
                        href === path ||                      // Exact file match
                        (path === '' && href === 'dashboard.html') // Root route matches dashboard.html only
                    );

                link.classList.toggle('active', isActive);
                if (isActive) {
                    link.setAttribute('aria-current', 'page');
                } else {
                    link.removeAttribute('aria-current');
                }
            });
        } catch (e) {
            // no-op
        }
    }

    destroy() {
        this.isInitialized = false;
    }
}

// Add notification animation styles
const styleSheet = document.createElement('style');
styleSheet.textContent = `
    @keyframes slideIn {
        from {
            opacity: 0;
            transform: translateX(20px);
        }
        to {
            opacity: 1;
            transform: translateX(0);
        }
    }
    @keyframes slideOut {
        from {
            opacity: 1;
            transform: translateX(0);
        }
        to {
            opacity: 0;
            transform: translateX(20px);
        }
    }
    .sr-only {
        position: absolute;
        width: 1px;
        height: 1px;
        padding: 0;
        margin: -1px;
        overflow: hidden;
        clip: rect(0, 0, 0, 0);
        white-space: nowrap;
        border: 0;
    }
`;
document.head.appendChild(styleSheet);

// Back to Top Button functionality
class BackToTopButton {
    constructor() {
        this.button = document.getElementById('back-to-top');
        this.scrollThreshold = 200; // Show button after scrolling this many pixels
        
        if (this.button) {
            this.init();
        }
    }
    
    init() {
        // Handle scroll events
        let ticking = false;
        window.addEventListener('scroll', () => {
            if (!ticking) {
                window.requestAnimationFrame(() => {
                    this.handleScroll();
                    ticking = false;
                });
                ticking = true;
            }
        });
        
        // Handle click
        this.button.addEventListener('click', () => {
            this.scrollToTop();
        });
        
        // Initial check
        this.handleScroll();
    }
    
    handleScroll() {
        if (window.scrollY > this.scrollThreshold) {
            this.button.classList.add('visible');
        } else {
            this.button.classList.remove('visible');
        }
    }
    
    scrollToTop() {
        window.scrollTo({
            top: 0,
            behavior: 'smooth'
        });
    }
}

// Initialize navigation when DOM is ready
document.addEventListener('DOMContentLoaded', () => {
    window.navigationManager = new NavigationManager();
    window.backToTopButton = new BackToTopButton();
});

// Export for module systems if needed
if (typeof module !== 'undefined' && module.exports) {
    module.exports = NavigationManager;
}


